.wrapperSvg {

}

.logo {
  width: mixins.rem(36);
  height: mixins.rem(36);
  background-color: #FFFFFF;
  border-radius: 50%;
}

.text_info {
  font-family: globalIndex.$font_family_main;
  line-height: globalIndex.$line_height;
  color: #FFFFFF;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  row-gap: mixins.rem(5);
}

.title {
  font-size: mixins.rem(13);
}

.subtitle {
  font-size: mixins.rem(10);
}

.profileInfo {
  position: absolute;
  bottom: 0;
  left: 0;
  min-width: 100%;
  transform: translate(mixins.rem(10), -120%);
  display: flex;
  column-gap: mixins.rem(10);
  align-items: center;
}

.progressBarContainer_cover {
  position: absolute;
  bottom: mixins.rem(5);
  left: 0;
  width: 100%;
  justify-content: center;
  transform: translate(mixins.rem(0), mixins.rem(-20));
  height: mixins.rem(10);
  display: flex;
  align-items: center;

}

.progressBarContainer {
  height: mixins.rem(6);
  background: rgba(255, 255, 255, 0.2);
  cursor: pointer;
  display: flex;
  align-items: center;
  position: relative;
  min-width: 80%;
}

.progressBar {
  height: 100%;
  background: #ffffff;
  transition: width 0.2s ease-in-out;
}

.progressThumb {
  position: absolute;
  width: mixins.rem(10);
  height: mixins.rem(10);
  background: #ffffff;
  border-radius: 50%;
  transform: translate(-100%, -50%);
  top: 50%;
}

.videoTime {
  color: #ffffff;
  font-size: mixins.rem(14);
  margin-left: mixins.rem(8);
  font-family: globalIndex.$font_family_main;
}

.logo_out {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(mixins.rem(10), mixins.rem(20));
  background-color: #FFFFFF;
  color: #b3b0b0;
  opacity: 0.5;
  height: mixins.rem(54);
  width: mixins.rem(57);
  border-radius: mixins.rem(50) mixins.rem(10) mixins.rem(50) mixins.rem(50);
  font-weight: 800;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: mixins.rem(16);
  box-shadow: mixins.rem(0) mixins.rem(0) mixins.rem(10) rgba(0, 0, 0, 0.2);
}

.replayButton {
  position: absolute;
  font-family: globalIndex.$font_family_main;
  font-size: mixins.rem(20);
  color: #FFFFFF;
  background-color: #292929;
  top:50%;
  left: 50%;
  padding-top: mixins.rem(10);
  padding-bottom: mixins.rem(10);
  padding-left: mixins.rem(30);
  padding-right: mixins.rem(30);
  border-radius: mixins.rem(15);
  transform: translate(-50%,-50%);
  transition: background-color ease 0.3s 0s;

  @media (any-hover: hover) {
    &:hover {
      transition: background-color ease 0.3s 0s;
      background-color: #2b2929;
    }
  }
}


.svg_play {
  width: mixins.rem(100);
  height: mixins.rem(100);
  fill: #FFFFFF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
